header {
  display: flex;
  margin: 0 auto;
  text-align: center;
}
.headerImg {
    background-image: url('../img/moonlight.jpg');
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    padding-top: 10px;
}
.header-title {
    font-family: sans-serif;
    margin-bottom: 50px;
    font-size: 4rem;
    color: rgb(240, 232, 240);
    padding-top: 50px;
    text-align: center;
}
  .header-p {
    text-align: center;
    margin: 5%;
    font-weight: bold;
    color:rgb(247, 240, 247);
    font-weight: 700;
    font-size: 1.75rem;
    font-family: sans-serif;
  }

  @media only screen and (max-width: 800px) {
    .headerImg {
      background-position: 80%;
    }
    .header-title {
      font-size: 2rem;
      padding-top: 90px;
    }
  }
  @media only screen and (max-width: 1200px) {
    .headerImg {
      background-position: 80%;
    }
  }